<!--
* @author: jinxl
* @date: 2018/3/26
* @module:工作站点安装
-->
<template>
<div class="site-install">
  <div class="w-table">
    <div class="w-table-btn">
      <span class="active"><i class="iconfont icon-conf-complete"></i>确认完成</span>
      <span @click="newAdd=true"><i class="iconfont icon-new-add"></i>新增</span>
      <span><i class="iconfont icon-export"></i>导出</span>
      <span @click="chart=true;"><i class="iconfont icon-job-weight"></i>工作比重</span>
    </div>
    <div class="w-table-content">
      <el-table
        :data="installTableData"
        style="width: 100%">
        <el-table-column
          prop="serialNumber"
          label="序号"
          align="center">
        </el-table-column>
        <el-table-column
          prop="departmentRom"
          label="科室名称">
        </el-table-column>
        <el-table-column
          prop="needSoftware"
          align="center"
          label="所需软件">
          <template slot-scope="scope">
            <el-select v-model="scope.row.needSoftware"
                       size="mini"
                       placeholder="请选择">
              <el-option
                v-for="item in selectData.needSoftware"
                :key="item.value"
                :label="item.label"
                :value="item.value">
              </el-option>
            </el-select>
          </template>
        </el-table-column>
        <el-table-column
          align="center"
          label="所需硬件">
          <template slot-scope="scope">
            <el-select v-model="scope.row.needHardware"
                       size="mini"
                       placeholder="请选择">
              <el-option
                v-for="item in selectData.needHardware"
                :key="item.value"
                :label="item.label"
                :value="item.value">
              </el-option>
            </el-select>
          </template>
        </el-table-column>
        <el-table-column
          align="center"
          label="是否本期范围">
          <template slot-scope="scope">
            <el-select v-model="scope.row.currentScope"
                       size="mini"
                       placeholder="请选择">
              <el-option
                v-for="item in selectData.currentScope"
                :key="item.value"
                :label="item.label"
                :value="item.value">
              </el-option>
            </el-select>
          </template>
        </el-table-column>
        <el-table-column
          align="center"
          label="分配给">
          <template slot-scope="scope">
            <el-select v-model="scope.row.assignedTo"
                       size="mini"
                       placeholder="请选择">
              <el-option
                v-for="item in selectData.assignedTo"
                :key="item.value"
                :label="item.label"
                :value="item.value">
              </el-option>
            </el-select>
          </template>
        </el-table-column>
        <el-table-column
          align="center"
          label="站点数">
            <template slot-scope="scope">
              <el-select v-model="scope.row.siteNumber"
                         size="mini"
                         placeholder="请选择">
                <el-option
                  v-for="item in selectData.siteNumber"
                  :key="item.value"
                  :label="item.label"
                  :value="item.value">
                </el-option>
              </el-select>
          </template>
        </el-table-column>
        <el-table-column
          align="center"
          label="状态">
          <template slot-scope="scope">
            <span :class="scope.row.stateClass">{{ scope.row.stateTxt }}</span>
          </template>
        </el-table-column>
        <el-table-column
          label="操作">
          <template slot-scope="scope">
            <div class="operate">
              <i @click="newPage = true;" class="iconfont icon-edit"></i>
              <i class="iconfont icon-delete"></i>
            </div>
          </template>
        </el-table-column>
      </el-table>
    </div>
  </div>
  <!--实施人员工作比重-->
  <el-dialog title="实施人员工作比重"
             width="50%"
             @open="showEchart"
             :visible.sync="chart">
    <div id="main"></div>
  </el-dialog>
  <transition
    leaveActiveClass="animated fadeOutRight"
    enterActiveClass="animated bounceInRight">
  <div class="new-page" v-show="newPage">
    <div class="new-page_header">
      <span class="new-page_title">挂号处站点安装</span>
      <button @click="closeNewPage"
              type="button"
              class="el-dialog__headerbtn">
        <i class="el-icon el-icon-close"></i>
      </button>
    </div>
    <div class="new-page_body">
      <el-form ref="form"
               :model="pageForm"
               label-width="80px"
               class="page-form-check">
        <el-form-item label="所需软件">
          <el-checkbox-group v-model="pageForm.needSoftware">
            <el-checkbox label="HIS前台" name="type"></el-checkbox>
            <el-checkbox label="THS前台" name="type"></el-checkbox>
            <el-checkbox label="HIsS前台" name="type"></el-checkbox>
            <el-checkbox label="IS前台" name="type"></el-checkbox>
          </el-checkbox-group>
        </el-form-item>
        <el-form-item label="所需硬件">
          <el-checkbox-group v-model="pageForm.needHardware">
            <el-checkbox label="台式机" name="type"></el-checkbox>
            <el-checkbox label="发票打印机" name="type"></el-checkbox>
            <el-checkbox label="报价机" name="type"></el-checkbox>
            <el-checkbox label="多种刷卡机" name="type"></el-checkbox>
            <el-checkbox label="多屏" name="type"></el-checkbox>
          </el-checkbox-group>
        </el-form-item>
      </el-form>
      <div class="line"></div>
      <div>
        <span class="icon-button"><i class="iconfont icon-new-add"></i>新增站点</span>
      </div>
      <div class="add-site">
        <div class="add-site_title">
          <span>站点1</span><i class="el-icon-delete"></i>
        </div>
        <div class="add-site_body">
          <el-form ref="pageForm"
                   :model="pageForm"
                   label-width="80px"
                   size="medium">
            <el-row :gutter="40">
              <el-col :md="12" :lg="8">
                <el-form-item label="客户名称">
                  <el-input v-model="pageForm.siteName"></el-input>
                </el-form-item>
              </el-col>
              <el-col :md="12" :lg="8">
                <el-form-item label="IP地址">
                  <el-input v-model="pageForm.ipAddress"></el-input>
                </el-form-item>
              </el-col>
              <el-col :md="12" :lg="8">
                <el-form-item label="楼宇">
                  <el-input v-model="pageForm.building"></el-input>
                </el-form-item>
              </el-col>
              <el-col :md="12" :lg="8">
                <el-form-item label="楼层">
                  <el-input v-model="pageForm.floor"></el-input>
                </el-form-item>
              </el-col>
              <el-col :md="12" :lg="8">
                <el-form-item label="PC机型号">
                  <el-input v-model="pageForm.pc"></el-input>
                </el-form-item>
              </el-col>
              <el-col :md="12" :lg="8">
                <el-form-item label="安装情况">
                  <el-radio v-model="pageForm.install" label="1">已安装</el-radio>
                  <el-radio v-model="pageForm.install" label="2">未安装</el-radio>
                </el-form-item>
              </el-col>
            </el-row>
            <el-row>
              <el-col :md="12" :lg="12">
                  <el-form-item label="上传照片">
                    <transition
                      leaveActiveClass="animated fadeOutRight"
                      enterActiveClass="animated bounceInRight">
                      <div v-show="uploadImg">
                        <el-upload
                          action=""
                          list-type="picture-card">
                          <i class="el-icon-plus"></i>
                        </el-upload>
                        <el-dialog :visible.sync="dialogVisible" size="samll">
                          <img width="100%" :src="dialogImageUrl" alt="">
                        </el-dialog>
                      </div>
                    </transition>
                  </el-form-item>
                </el-col>
              <el-col :md="12" :lg="12">
                <div class="page-form-show">
                  展开<i class="el-icon-arrow-down"></i>
                </div>
              </el-col>
            </el-row>
          </el-form>
        </div>
      </div>
    </div>
  </div>
  </transition>
  <!--新增-->
  <el-dialog title="新增"
             width="50%"
             @open="showEchart"
             :visible.sync="newAdd">
    <el-form :model="formData" label-width="80px" size="medium">
      <el-row>
        <el-col :md="8" :lg="8">
          <el-form-item label="科室类别">
            <el-input v-model="formData.departType"></el-input>
          </el-form-item>
        </el-col>
        <el-col :md="8" :lg="8">
          <el-form-item label="科室类别">
            <el-input v-model="formData.departName"></el-input>
          </el-form-item>
        </el-col>
        <el-col :md="8" :lg="8">
          <el-form-item label="使用系统">
            <el-select v-model="formData.userSystem" placeholder="请选择">
              <el-option
                v-for="item in selectFormData"
                :key="item.value"
                :label="item.label"
                :value="item.value">
              </el-option>
            </el-select>
          </el-form-item>
        </el-col>
      </el-row>
    </el-form>
    <span slot="footer" class="dialog-footer">
      <el-button size="small" @click="newAdd = false">取 消</el-button>
      <el-button size="small" type="primary" @click="newAdd = false">保 存</el-button>
    </span>
  </el-dialog>
</div>
</template>
<!--JavaScript-->
<script>
    import echarts from 'echarts'
    export default {
      data(){
        return {
            installTableData:[
              {
                  serialNumber:1,
                  departmentRom:'挂号处',
                  needSoftware:'选项2',
                  needHardware:'选项2',
                  currentScope:'',
                  assignedTo:'',
                  siteNumber:6,
                  stateTxt:'已安装',
                  stateClass:'installed'
              },
              {
                serialNumber:2,
                departmentRom:'挂号处',
                needSoftware:'选项1',
                needHardware:'选项2',
                currentScope:'',
                assignedTo:'',
                siteNumber:10,
                stateTxt:'未安装',
                stateClass:'noinstall'

              },
              {
                serialNumber:3,
                departmentRom:'挂号处',
                needSoftware:'选项1',
                needHardware:'选项2',
                currentScope:'',
                assignedTo:'',
                siteNumber:10,
                stateTxt:'安装中',
                stateClass:'installing'
              }
            ],
            selectData:{
                needSoftware:[
                  {
                    value: '选项1',
                    label: 'THIS前台'
                  }, {
                    value: '选项2',
                    label: 'LIS前台'
                  }
                ],
                needHardware:[
                  {
                    value: '选项1',
                    label: '台式机、发票打印机、凭单打印机...'
                  }, {
                    value: '选项2',
                    label: '2台式机、发票打印机、凭单打印机2...'
                  }
                ],
                currentScope:[
                  {
                    value: '选项1',
                    label: '是'
                  }, {
                    value: '选项2',
                    label: '否-本期不实施'
                  }
                ],
              assignedTo:[
                {
                  value: '选项1',
                  label: '三毛'
                }, {
                  value: '选项2',
                  label: '五毛'
                }
              ],
              siteNumber:[
                {
                  value: '10',
                  label: '10'
                }, {
                  value: '6',
                  label: '6'
                }
              ]
            },
            chart:false,
            newAdd:false,
            newPage:true,
            formData:{
                departType:'科室类别',
                departName:'科室名称',
                userSystem:'1'
            },
            selectFormData:[
              {
                value: '1',
                label: '门诊医生站'
              },
              {
                value: '2',
                label: '大屏系统'
              }
            ],
            pageForm:{
              needSoftware:[],
              needHardware:[],
              siteName:'',
              ipAddress:'',
              building:'',
              floor:'',
              pc:'',
              install:'1'
            },
            dialogImageUrl: '',
            dialogVisible: false,
            uploadImg:false
        }
      },
      created(){
          this.$nextTick(function(){
          })
      },
      methods:{
        showEchart:function () {
          let option = {
            color: ['#3398DB'],
            tooltip : {
              trigger: 'axis',
              axisPointer : {
                type : 'shadow'
              }
            },
            grid: {
              left: '3%',
              right: '4%',
              bottom: '3%',
              containLabel: true
            },
            xAxis : [
              {
                type : 'category',
                data : ['小毛', '二毛', '三毛', '四毛', '五毛', '六毛', '七毛','八毛','小毛', '二毛', '三毛', '四毛', '五毛', '六毛', '七毛','八毛'],
                axisTick: {
                  alignWithLabel: true
                },
                axisLabel:{
                  color:'#666666',
                  fontFamily:'Microsoft YaHei'
                },
                axisLine:{
                  show:false
                }
              }
            ],
            yAxis : [
              {
                type : 'value',
                axisLabel:{
                  color:'#93abba'
                },
                axisLine:{
                  lineStyle:{
                    color:'#93abba'
                  }
                }
              }
            ],
            series : [
              {
                type:'bar',
                label:{
                  show:true,
                },
                data:[10, 52, 200, 334, 90, 330, 20,60,95, 52, 200, 334, 30, 330, 520,60],
                markPoint : {
                  data : [
                    {type : 'max', name: '最大值'},
                    {type : 'min', name: '最小值'}
                  ]
                },
                markLine : {
                  data : [
                    {type : 'average', name: '平均值'}
                  ]
                },
                itemStyle:{
                  color:'#4d9eff'
                }
              }
            ]
          };
          setTimeout(_=>{
            let myChart = echarts.init(document.getElementById('main'));
            myChart.setOption(option);
          },0);
        },
        closeNewPage:function () {
          this.newPage = false;
        }
      }
    }

</script>
<!--CSS-PAGE-->
<style lang="stylus" scoped>
  .site-install{
    position relative
    height 100%
  }
  #main{
    width 100%
    height 400px
  }
  span.installed{
    color:#2fdd2f;
  }
  span.noinstall{
    color:#ff6464;
  }
  span.installing{
    color:#666666;
  }
  .new-page{
    position absolute
    top 0px
    right 0px
    bottom 0px
    left 0px
    z-index 10
    background #fff
    overflow auto
    border 1px solid red
  }
  .new-page_header{
      padding: 20px 20px 10px;
  }
  .new-page_title{
    line-height: 24px;
    font-size: 16px;
    color: #333;
  }
  .new-page_body{
    padding: 0px 20px;
    color: #606266;
    line-height: 24px;
    font-size: 14px;
  }
  .page-form-check .el-form-item{
    margin-bottom 0px
  }
  .icon-button{
    position: relative;
    margin-right: 30px;
    padding 4px 15px;
    cursor: pointer;
    color: #606266;
    font-size 14px;
    border-radius 3px
    background: #fff;
    border: 1px solid #dcdfe6;
  }
  .icon-button>i{
    margin-right: 10px;
    font-size: 14px;
  }
  .line{
    margin  30px 0px
    border-bottom:1px dashed #f2f2f2
  }
  .add-site{
    margin-top 20px
  }
  .add-site_title{
    margin-bottom 20px
  }
  .add-site_title>span{
    padding-left 8px
    border-left 2px solid #1e90ff
    color #333
    font-size 14px
  }
  .add-site_title>i{
    float right
    cursor pointer
    width 30px
    height 24px
    text-align center
  }
  .add-site_title>i.el-icon-delete:before{
    vertical-align sub
  }
</style>
<style lang="stylus">
  .add-site_body .el-upload--picture-card{
    width 100px
    height 100px
    line-height 100px
  }
  .page-form-show{
    width 60px
    margin-right 0px
    margin-left auto
    text-align right
    border 1px solid red
    cursor pointer
    transition transform ease-in .5s
  }
  .page-form-show i{
    transform rotate(-180deg)
  }
</style>



